export default {
	onLoad() {
		if (uni.getStorageSync('remark')) {
			this.remarkDetail = uni.getStorageSync('remark')
		}
	},
	data () {
		return {
			remarkDetail: ''
		}
	},
	methods: {
		submit () {
			uni.setStorageSync('remark', this.remarkDetail)
			uni.navigateBack({
				delta: 1
			})
		}
	}
}
